.register-main-container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    padding: 20px;
    background: linear-gradient(135deg, #f5e9ff 50%, #d6b4fc 50%);
    font-family: 'Poppins', sans-serif;
}

.register-form-wrapper {
    display: flex;
    width: 900px;
    max-width: 100%;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
    border-radius: 20px;
    overflow: hidden;
    background: #fff;
    flex-direction: row;
}

.register-left {
    flex: 1;
    padding: 40px;
    background-color: #fff;
}

.register-left h2 {
    font-size: 28px;
    margin-bottom: 10px;
    color: #333;
    font-weight: 500;
    text-align: center;
}

.login-pass-wrapper-2 {
    position: relative;
}

.register-pass {
    width: 100%;
}

.toggle-password-icon-login-2 {
    position: absolute;
    top: 42%;
    transform: translateY(-50%);
    right: 12px;
}

.register-left p {
    margin-bottom: 30px;
    color: #888;
    text-align: center;
}

.register-left form {
    display: flex;
    flex-direction: column;
}

.register-left input {
    padding: 12px 15px;
    margin-bottom: 15px;
    border: 1px solid #ccc;
    border-radius: 8px;
    font-size: 14px;
}

.register-left button {
    padding: 12px;
    background: #a855f7;
    color: white;
    border: none;
    border-radius: 8px;
    font-weight: 600;
    font-size: 16px;
    cursor: pointer;
    margin-top: 10px;
    transition: background 0.3s;
}

.register-left button:hover {
    background: #9333ea;
}

.register-left a {
    font-size: 14px;
    color: #222;
    text-align: center;
    margin-top: 10px;
    text-decoration: none;
}

.register-left a span {
    color: #a855f7;
}

.register-left a:hover {
    text-decoration: underline;
}

.register-right {
    flex: 1;
    background: url('../../assets/llogin-right.jpg  ') no-repeat center center/cover;
}

/* ---------- Responsive ---------- */
@media (max-width: 768px) {
    .register-form-wrapper {
        flex-direction: column;
        border-radius: 12px;
    }

    .register-left {
        padding: 30px 20px;
    }

    .register-right {
        height: 300px;
        width: 100%;
    }
}

@media (max-width: 480px) {
    .register-left h2 {
        font-size: 22px;
    }

    .register-left p {
        font-size: 14px;
    }

    .register-left input,
    .register-left button {
        font-size: 14px;
    }
}